.main-navigation {
  width: auto;
}
@media (max-width: 767px) {
  .main-navigation {
    position: relative;
    min-height: 198px;
  }
}
@media (min-width: 768px) {
  .main-navigation {
    width: 62.14%;
    /* было 552px */
    max-width: 806px;
    padding-left: 49px;
  }
}
@media (min-width: 1300px) {
  .main-navigation {
    width: 518px;
  }
}
@media (max-width: 767px) {
  .main-navigation--closed {
    min-height: 15px;
  }
}
.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  top: 0;
  left: 0;
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  top: 8px;
  left: 1px;
  background-color: #222222;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__item {
    display: none;
  }
}
.main-navigation__toggle {
  position: absolute;
  top: -28px;
  left: 276px;
  padding: 0;
  border: 0 none;
  font: inherit;
  width: 25px;
  height: 19px;
  background-color: #ffffff;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}
.main-navigation--nojs .main-navigation__toggle {
  display: none;
}
.main-navigation--nojs .main-navigation__list {
  position: static;
  min-height: 0;
}
.main-navigation--nojs .main-navigation__item {
  display: block;
}
.main-navigation__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
